.services {
  &__info {
    padding-left: 310px;
    padding-top: 35px;
    padding-bottom: 35px;
  }
  &__content-wrapper {
    padding-left: 50px;
  }
  &__title {
    font: 400 30px / 27px 'GnuolaneFree', sans-serif;
    color: $titlecolor;
    margin-bottom: 30px;
  }
  &__text {
    font: 400 14px / 24px 'ArialMT', sans-serif;
    opacity: 0.9;
    color: $fontcolor;
    margin-bottom: 15px;
  }
  &__descr {
    padding-bottom: 10px;
  }
  &__subtitle {
    font: 400 16px / 27px 'GnuolaneFree', sans-serif;;
    color: $titlecolor;
    margin-bottom: 5px;
  }
}